// Login.js

import React, {useEffect, useState} from 'react';
import { useNavigate } from 'react-router-dom'; // React Router useHistory 
import axios from "axios";
import '../CSS/Join.css'
export default function Login() {
    const navigate = useNavigate(); // useHistory     
    const [user, setUser] = useState({
        id: '',
        password: ''
    });


    const handleChange = (e) => {
        const fieldName = e.target.name;
        const value = e.target.value;
        setUser({ ...user, [fieldName]: value });
    };

    const handleLogin = async (e) => {
        e.preventDefault();
        try {
            const userData = {
                id: user.id,
                password: user.password
            };
            const response = await axios.post("/Login/login", userData);
            const message = response.data.message;
            const sessionData = response.data.sessionData;
            if (message === " ") {
                console.log(" !");
                navigate('/Login/main',{state :  {userId: sessionData}});
            } else {
                console.error(" ");
            }
        } catch (error) {
            console.error(" :", error);
        }
    };

    return (
        <form className="loginForm" onSubmit={handleLogin}>
            <div className="inputContainer">
                <label>Id:</label>
                <input
                    type="text"
                    name="id"
                    value={user.id}
                    onChange={handleChange}
                />
            </div>
            <div className="inputContainer">
                <label>Password:</label>
                <input
                    type="password"
                    name="password"
                    value={user.password}
                    onChange={handleChange}
                />
            </div>
            <button type="submit"></button>
        </form>
    );
}
